.red{
    color: #f00606
   }
   .green{
       color: #11d811
   }
   .coin-container{
    width: 95vw;
    display: flex;
    justify-content: center;
}
.coin{
    display: flex;
    align-items: center;
    /* padding-right: 24px; */
    min-width: 300px;
}
.coin-symbol{
    text-transform: uppercase;
    margin-left: 2rem;
}
.coin img{
    height: 30px;
    width: 30px;
    margin-right: 10px;
}
.coin h1{
    font-size: 2rem;
    width: 150px;
}
.coin-row{
    border-bottom: 1px solid #d7d7d7;
    margin: 1rem;
}
.coin-marketcap{
    padding-bottom: .5rem;
}

@media only screen and (min-width: 50em) {
.coin-container{
    display: flex;
    justify-content: center;
}
.coin-row{
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
}

.coin{
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}

.coin img{
    height: 30px;
    width: 30px;
    margin-right: 10px;
}
.coin h1{
    font-size: 16px;
    width: 150px;
}

.coin-data{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin-price{
    width: 230px;
    padding-right: 1rem;
}

.coin-volume{
    width: 155px;
}
.coin-marketcap{
    width: 155px;
}
.coin-percent{
    width: 100%;
}
}